

.player {
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    color: white;
  }
  
  .time-control {
    width: 50%;
    display: flex;
    text-shadow: 2px 2px black;
    input {
      width: 100%;
      padding: 1rem 0rem;
      -webkit-appearance: none;
      background: transparent;
      cursor: pointer;
    }
    p {
      padding: 1rem;
    }
  }
  
  .play-control {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 30%;
    padding: 1rem;
    svg {
      cursor: pointer;
    }
  }
  
  input[type="range"]:focus {
    outline: none;
  }
  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 16px;
    width: 16px;
  }
  input[type="range"]::-moz-slider-thumb {
    -webkit-appearance: none;
    background: transparent;
    width: 16px;
  }
  
  .track {
    width: 100%;
    height: 1rem;
    position: relative;
    margin-top: 1.1rem;
    border-radius: 1rem;
    overflow: hidden;
  }
  
  .animate-track {
    background: rgb(255, 255, 255);
    width: 100%;
    height: 100%;
    position: absolute;
  
    top: 0;
    left: 0;
    transform: translateX(0%);
    pointer-events: none;
  }
  
  @media screen and (max-width: 768px) {
    .time-control {
      width: 90%;
    }
    .play-control {
      width: 60%;
    }
  }
  